* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  body {
    background: #121212;
    color: #e8ffe8;
  }
  
  
  .sidebar {
    width: 250px;
    background: #1b1b1b;
    border-right: 1px solid #333;
    padding: 1.5rem 1rem;
    overflow-y: auto;
    border-radius: 0 20px 20px 0;
  }
  
  .logo {
    display: flex;
    align-items: center;
    gap: 0.7rem;
    margin-bottom: 1.5rem;
  }
  
  .logo h1 {
    font-size: 1.2rem;
    color: #28a745;
  }
  
  .star-btn {
    background: none;
    border: none;
    font-size: 1.4rem;
    color: #28a745;
    cursor: pointer;
  }
  
  nav ul {
    list-style: none;
  }
  
  nav li {
    padding: 0.5rem 0.7rem;
    border-radius: 12px;
    cursor: pointer;
    color: #e6ffe6;
  }
  
  nav li:hover {
    background: #2a2a2a;
  }
  
  .new-chat-btn {
    background: #28a745;
    color: #000;
    font-weight: bold;
    text-align: center;
    padding: 0.6rem;
    border-radius: 12px;
    cursor: pointer;
    margin-bottom: 1rem;
  }
  
  .new-chat-btn:hover {
    background: #1f7d38;
  }
  

  main {
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  
  .topbar {
    display: flex;
    gap: 1rem;
    padding: 1rem;
    background: #1e1e1e;
    border-radius: 0 0 20px 20px;
  }
  
  .topbar button {
    background: none;
    border: none;
    color: #28a745;
    font-size: 1rem;
    padding: 0.5rem 1rem;
    border-radius: 12px;
    cursor: pointer;
  }
  
  .topbar .active,
  .topbar button:hover {
    background: #28a745;
    color: #000;
  }
  
  .hero {
    padding: 2rem;
    flex: 1;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
  }

  .bubble {
    max-width: 70%;
    padding: 0.8rem 1rem;
    border-radius: 20px;
    font-size: 0.95rem;
    white-space: pre-wrap;
    word-wrap: break-word;
  }
  
  .bubble.user {
    background: #28a745;
    color: black;
    align-self: flex-end;
    margin-left: auto;
  }
  
  .bubble.ai {
    background: #222;
    color: #e6ffe6;
    align-self: flex-start;
    margin-right: auto;
  }
  
 
  .chat-bar {
    display: flex;
    padding: 1rem 2rem;
    background: #1e1e1e;
    border-radius: 20px 20px 0 0;
    align-items: center;
    gap: 1rem;
  }
  
  .chat-bar input {
    flex: 1;
    padding: 0.7rem 1rem;
    border-radius: 12px;
    border: none;
    outline: none;
    background: #2c2c2c;
    color: #e6ffe6;
  }
  
  .chat-bar input::placeholder {
    color: #888;
  }
  
  .send-btn {
    background: #28a745;
    border: none;
    padding: 0.7rem 1.2rem;
    border-radius: 12px;
    cursor: pointer;
    font-weight: bold;
  }
  
  .send-btn:hover {
    background: #1f7d38;
  }
  

  footer {
    padding: 1rem 2rem;
    background: #121212;
    font-size: 0.85rem;
    color: #aaa;
  }
  

  .chat-entry {
    padding: 0.5rem 0.7rem;
    border-radius: 10px;
    cursor: pointer;
    margin-top: 4px;
  }
  
  .chat-entry:hover {
    background-color: #2a2a2a;
  }
  
  .app {
    display: flex;
    height: 100vh;
  }
  

  .app {
    display: flex;
    height: 100vh;
  }
  
  .home-page {
    height: 100vh;
    background: linear-gradient(to right, #121212, #1b1b1b);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #e8ffe8;
    gap: 2rem;
  }
  
  .home-title {
    font-size: 2.5rem;
    color: #28a745;
  }
  
  .home-buttons button {
    padding: 1rem 2rem;
    font-size: 1rem;
    border-radius: 12px;
    border: none;
    cursor: pointer;
    margin: 0 1rem;
    transition: 0.3s;
  }
  
  .login-btn {
    background: #28a745;
    color: #000;
  }

  form > .login-btn {
    border-radius: 20px;
    border: none;
    height: 30px;
  }

  .register-btn {
    background: transparent;
    border: 2px solid #28a745;
    color: #28a745;
  }

  form > .register-btn {
    border-radius: 20px;
    height: 30px;
  }
  
  .login-btn:hover,
  .register-btn:hover {
    background: #1f7d38;
    color: #fff;
  }
  
  .admin-page {
    padding: 2rem;
    background: #121212;
    color: #e8ffe8;
    min-height: 100vh;
  }
  
  .user-cards {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-top: 1rem;
  }
  
  .user-card {
    background: #1b1b1b;
    padding: 1rem;
    border-radius: 12px;
    box-shadow: 0 0 8px #28a74533;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .admin-actions button {
    margin-right: 1rem;
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 8px;
    cursor: pointer;
  }
  
  .admin-actions button:first-child {
    background: #28a745;
    color: #000;
  }
  
  .admin-actions button:last-child {
    background: #dc3545;
    color: #fff;
  }
  
  .auth-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 300px;
    background: #1b1b1b;
    padding: 2rem;
    border-radius: 16px;
    box-shadow: 0 0 8px #28a74533;
  }
  
  .auth-form input {
    padding: 0.7rem 1rem;
    border-radius: 8px;
    border: none;
    background: #2c2c2c;
    color: #e8ffe8;
  }
  
  .auth-form input::placeholder {
    color: #aaa;
  }
  
  .logout-btn {
    margin-left: auto;
    background: #28a745;
    color: #000;
    font-weight: bold;
    border-radius: 8px;
    padding: 0.4rem 0.8rem;
    cursor: pointer;
    border: none;
    transition: background 0.2s;
  }
  
  .logout-btn:hover {
    background: #1f7d38;
  }
  